.sidebar {
    color: rgb(55, 53, 47);
    background-color: rgb(251, 251, 250);
    width: 30%;
    margin: 0;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 15px;
}

.profile {
    width: 35px;
    height: 100%;
    object-fit: cover;
    border-radius: 2.5px;
}

@media (max-width: 800px) {
    .sidebar {
        display: none;
    }
}